<template>
  <div class="about">
    <!-- <h1>This is an about page</h1> -->
    <div class="HotBox">
      <div class="title"></div>
      <div class="UpDate">
        更新日期:<span>{{ time | timeFormater }}</span>
      </div>
    </div>
    <ul class="home-songlist">
      <SongListItem
        v-for="(item, index) in hotsongs"
        :key="item.id"
        :item="item"
        @change-current-song="
          $emit('change-current-song', $event);
          $emit('change-current-play-list', hotsongs);
        "
        :currentSongId="currentSongId"
        :playing="playing"
        :class="{ lt3: index < 3 }"
        >{{ index + 1 }}</SongListItem
      >
    </ul>
  </div>
</template>

<script>
import dayjs from "dayjs";
import SongListItem from "@/components/SongListItem.vue";
export default {
  name: "Hot",
  components: {
    SongListItem,
  },
  props: {
    currentSongId: {
      type: Number,
    },
    playing: Boolean,
  },
  data: function () {
    return {
      hotsongs: [],
      time: Date.now(),
    };
  },
  filters: {
    // es6形参默认值 str = '数值'
    timeFormater(value, str = "MM月DD日 HH:mm:ss") {
      return dayjs(value).format(str);
    },
  },
  created() {
    this.axios.get("/playlist/detail?id=3778678").then((res) => {
      // console.log(this, res);
      this.hotsongs = res.data.playlist.tracks.slice(0, 20);
    });
  },
};
</script>

